body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.main-wrapper {
  background: white;
  width: 100vw;
  height: 100vh;
  padding: 30px;
  max-width: 320px;
  border: 1px solid grey;
  margin: 0 auto;
  position: relative;
}

.form-button {
  background: rgb(97, 165, 228);
  width: 100%;
  color: white;
  font-size: 30px;
  margin-bottom: 10px;
  font-weight: 600;
  border: 1px solid rgb(97, 165, 228);
  padding-bottom: 2px;
}


.smiley-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 15px;
  font-family: Arial, Helvetica, sans-serif;
}

.smiley {
  width: 75px;
  height: 75px;
  border-radius: 50px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: 36px;
  padding-bottom: 6px;
  padding-right: 3px;
  font-weight: 600;
}

.good {
  background: rgb(255, 230, 0);
  color: rgb(66, 66, 66);
}

.bad {
  background: rgb(255, 102, 0);
  color: white;
}
.form-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  background: white;
  width: 100%;
  height: 100%;
  padding: 30px;
  font-family: Arial, Helvetica, sans-serif;
}

.date-input {
  display: block;
  width: 100%;
  height: 30px;
  border: 1px solid lightgrey; 
  border-radius: 2px;
  margin-top: 5px;
  color: grey;
  padding: 10px;
}

.date-input::-webkit-inner-spin-button {
  display: none;
  -webkit-appearance: none;
}

.state-label {
  display: block;
  margin-top: 20px;
}

.mood-input-wrapper {
  display: inline-block;
  margin-top: 10px;
  margin-right: 50px;
  border-style: none;
}

.mood-label {
  margin-left: 10px;
}

.message-wrapper {
  margin-top: 20px;
}

.message-input {
  display: block;
  width: 100%;
  height: 30px;
  border: 1px solid lightgrey; 
  border-radius: 2px;
  margin-top: 5px;
  padding: 10px;
  font-size: 12px;
}

.save-button {
  display: block;
  width: 100%;
  height: 30px;
  background-color: rgb(97, 165, 228);
  color: white;
  border: 1px solid  rgb(97, 165, 228);
  border-radius: 2px;
  margin-top: 32px;
  font-size: 14px;
  font-weight: 600;
}

.cancel-button {
  display: block;
  width: 100%;
  color: gray;
  margin-top: 10px;
  font-size: 12px;
}
